import React, { useState } from "react";
import "./App.scss";
import ButtonFilter from "./ButtonFilter.js";
import cats from "./cats.jpg";

const DEFAULT_FILTER_OPTIONS = [
  {
    name: "Brightness",
    property: "brightness",
    value: 100,
    min: 0,
    max: 500,
    units: "%",
  },
  {
    name: "Contrast",
    property: "contrast",
    value: 100,
    min: 0,
    max: 500,
    units: "%",
  },
  {
    name: "Grayscale",
    property: "grayscale",
    value: 0,
    min: 0,
    max: 100,
    units: "%",
  },
  {
    name: "HUE",
    property: "hue-rotate",
    value: 0,
    min: 0,
    max: 360,
    units: "deg",
  },
  {
    name: "Invert",
    property: "invert",
    value: 0,
    min: 0,
    max: 100,
    units: "%",
  },
  {
    name: "Opacity",
    property: "opacity",
    value: 100,
    min: 0,
    max: 100,
    units: "%",
  },
  {
    name: "Saturation",
    property: "saturate",
    value: 100,
    min: 0,
    max: 1000,
    units: "%",
  },
  {
    name: "Sepia",
    property: "sepia",
    value: 0,
    min: 0,
    max: 100,
    units: "%",
  },
];

function App() {
  const [options, setOptions] = useState(DEFAULT_FILTER_OPTIONS);
  const [selectedOption, setSelectedOption] = useState(0);

  const currentOption = options[selectedOption];

  function handleRangeChange({ target }) {
    setOptions((options) => {
      return options.map((option, i) => {
        if (i === selectedOption) {
          return { ...option, value: target.value };
        }
        return option;
      });
    });
  }

  function setFilterStyle() {
    return {
      filter: options.reduce((total, option) => {
        return total + ` ${option.property}(${option.value}${option.units})`;
      }, " "),
    };
  }

  return (
    <div className="wrapper">
      <div className="photoshop">
        <div className="photoshop__container">
          <div className="photoshop__row">
            <div className="photoshop__image-and-range">
              <div className="photoshop__image-layout">
                <img
                  className="photoshop__img"
                  src={cats}
                  alt="Cats"
                  style={setFilterStyle()}
                />
              </div>
              <div className="photoshop__range-layout">
                <input
                  type="range"
                  min={currentOption.min}
                  max={currentOption.max}
                  value={currentOption.value}
                  onChange={handleRangeChange}
                  className="photoshop__range"
                />
              </div>
            </div>
            <aside className="photoshop__sidebar">
              {options.map((option, index) => {
                return (
                  <ButtonFilter
                    option={option}
                    isSelected={index === selectedOption}
                    handleChoice={() => {
                      setSelectedOption(index);
                    }}
                    key={index}
                  />
                );
              })}
            </aside>
          </div>
        </div>
      </div>
    </div>
  );
}

export default App;
